import React from 'react';
import './Register.css';
class Register extends React.Component {
    constructor(props){
        super();
        this.state = {
            email: '',
            password: '',
            name: '',
        }
    }

    onNameChange = (event) => {
        this.setState({name: event.target.value})
    }

    onEmailChange = (event) => {
        this.setState({email: event.target.value})
    }
    onPasswordChange = (event) => {
        this.setState({password: event.target.value})
    }

    onSubmitRegister = () => {
        fetch('https://warm-dawn-80266.herokuapp.com/register', {
            method: 'post',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
                email: this.state.email,
                password: this.state.password,
                name: this.state.name,
            })
        })
        .then(response => response.json())
        .then(user => {
            if (user.id){
             this.props.loadUser(user)
             this.props.onRouteChange('home');
            }
        })
    }

   render() {
       return (
        <article className="br3 ba black-10 mv4 w-50-m w-25-l mw6 shadow-5 center">
            <main className="pa4 black-80">
                <div className="measure">
                    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
                        <legend className="f1 fw6 ph0 mh0">Register</legend>
                        <div className="mt3 w-100">
                            <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                            <input onChange={this.onEmailChange} className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address" />
                        </div>
                        <div className="mt3 w-100">
                            <label className="db fw6 lh-copy f6" htmlFor="name">Name</label>
                            <input onChange={this.onNameChange} className="pa2 input-reset ba bg-transparent hover-bg-black hover-white wfix" type="name" name="name"  id="name" />
                        </div>
                        <div className="mt3 w-100">
                            <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
                            <input onChange={this.onPasswordChange} className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password" />
                        </div>
                            <label className="pa0 ma0 lh-copy f6 pointer"/>
                    </fieldset>
                        <div className="mt3">
                            <input 
                            onClick={this.onSubmitRegister}
                            className="b ph3 link pv2 input-reset ba b--black bg-transparent grow pointer f6 dib pointer" type='button' value="Register" />
                         </div>      
                </div>
            </main>
        </article>
       );
  }
}

export default Register;